// src/components/portfolio/TransactionForm.js
import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import usePortfolioStore from '../../store/portfolioStore';
import SearchableSelect from '../common/SearchableSelect';
import './TransactionForm.css';

const TransactionForm = ({ coins }) => {
  const { t } = useTranslation();
  const { cash, buyCoin, sellCoin, getHolding } = usePortfolioStore();

  const [transactionType, setTransactionType] = useState('buy'); // buy or sell
  const [selectedCoin, setSelectedCoin] = useState(null);
  const [amount, setAmount] = useState('');
  const [note, setNote] = useState('');
  const [message, setMessage] = useState({ text: '', type: '' });

  // ล้างฟอร์ม
  const resetForm = () => {
    setAmount('');
    setNote('');
    setMessage({ text: '', type: '' });
  };

  // Handle Submit
  const handleSubmit = (e) => {
    e.preventDefault();

    if (!selectedCoin) {
      setMessage({ text: 'Please select a coin', type: 'error' });
      return;
    }

    if (!amount || parseFloat(amount) <= 0) {
      setMessage({ text: 'Please enter a valid amount', type: 'error' });
      return;
    }

    const amountNum = parseFloat(amount);
    const price = selectedCoin.current_price;

    let result;
    if (transactionType === 'buy') {
      result = buyCoin(
        selectedCoin.id,
        selectedCoin.symbol.toUpperCase(),
        selectedCoin.name,
        amountNum,
        price,
        note
      );
    } else {
      result = sellCoin(
        selectedCoin.id,
        selectedCoin.symbol.toUpperCase(),
        amountNum,
        price,
        note
      );
    }

    if (result.success) {
      setMessage({ text: result.message, type: 'success' });
      resetForm();
      setTimeout(() => setMessage({ text: '', type: '' }), 5000);
    } else {
      setMessage({ text: result.message, type: 'error' });
    }
  };

  // คำนวณยอดรวม
  const calculateTotal = () => {
    if (!selectedCoin || !amount) return 0;
    return parseFloat(amount) * selectedCoin.current_price;
  };

  const total = calculateTotal();
  const holding = selectedCoin ? getHolding(selectedCoin.id) : null;

  return (
    <div className="transaction-form">
      <h2>💱 {transactionType === 'buy' ? t('transaction.buy') : t('transaction.sell')}</h2>

      {/* Transaction Type Toggle */}
      <div className="transaction-type-toggle">
        <button
          className={transactionType === 'buy' ? 'active buy' : 'buy'}
          onClick={() => {
            setTransactionType('buy');
            resetForm();
          }}
        >
          {t('transaction.buy')}
        </button>
        <button
          className={transactionType === 'sell' ? 'active sell' : 'sell'}
          onClick={() => {
            setTransactionType('sell');
            resetForm();
          }}
        >
          {t('transaction.sell')}
        </button>
      </div>

      <form onSubmit={handleSubmit}>
        {/* Coin Selection */}
        <div className="form-group">
          <label>{t('portfolio.coin')}</label>
          <SearchableSelect
            options={coins}
            value={selectedCoin}
            onChange={setSelectedCoin}
            placeholder="Select a coin..."
          />
          {selectedCoin && (
            <div className="coin-info">
              <img src={selectedCoin.image} alt={selectedCoin.name} />
              <span>{selectedCoin.name} ({selectedCoin.symbol.toUpperCase()})</span>
              <span className="price">${selectedCoin.current_price.toLocaleString()}</span>
            </div>
          )}
        </div>

        {/* Amount */}
        <div className="form-group">
          <label>{t('portfolio.amount')}</label>
          <input
            type="number"
            step="0.00000001"
            min="0"
            value={amount}
            onChange={(e) => setAmount(e.target.value)}
            placeholder="0.00"
          />
          {holding && transactionType === 'sell' && (
            <p className="help-text">
              Available: {holding.amount.toFixed(8)} {selectedCoin.symbol.toUpperCase()}
            </p>
          )}
        </div>

        {/* Note */}
        <div className="form-group">
          <label>{t('transaction.note')} (Optional)</label>
          <input
            type="text"
            value={note}
            onChange={(e) => setNote(e.target.value)}
            placeholder="Add a note..."
            list="note-suggestions"
          />
          <datalist id="note-suggestions">
            <option value="Long-term hold" />
            <option value="Day trading" />
            <option value="Take profit" />
            <option value="Stop loss" />
            <option value="DCA strategy" />
          </datalist>
        </div>

        {/* Summary */}
        {selectedCoin && amount && (
          <div className="transaction-summary">
            <div className="summary-row">
              <span>{t('portfolio.price')}:</span>
              <span>${selectedCoin.current_price.toLocaleString()}</span>
            </div>
            <div className="summary-row">
              <span>{t('portfolio.amount')}:</span>
              <span>{amount}</span>
            </div>
            <div className="summary-row total">
              <span>{t('portfolio.total')}:</span>
              <span>${total.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</span>
            </div>
            {transactionType === 'buy' && (
              <div className="summary-row">
                <span>{t('portfolio.cash')} after:</span>
                <span className={cash - total < 0 ? 'negative' : ''}>
                  ${(cash - total).toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}
                </span>
              </div>
            )}
            {transactionType === 'sell' && holding && (
              <div className="summary-row">
                <span>Profit/Loss:</span>
                <span className={selectedCoin.current_price - holding.avgBuyPrice >= 0 ? 'positive' : 'negative'}>
                  ${((selectedCoin.current_price - holding.avgBuyPrice) * parseFloat(amount)).toFixed(2)}
                </span>
              </div>
            )}
          </div>
        )}

        {/* Message */}
        {message.text && (
          <div className={`message ${message.type}`}>
            {message.text}
          </div>
        )}

        {/* Submit Button */}
        <button type="submit" className={`submit-btn ${transactionType}`}>
          {transactionType === 'buy' ? '🛒 Buy' : '💸 Sell'}
        </button>
      </form>

      {/* Available Cash */}
      <div className="available-cash">
        💵 {t('portfolio.cash')}: ${cash.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}
      </div>
    </div>
  );
};

export default TransactionForm;