import { create } from 'zustand';
import { persist } from 'zustand/middleware';

const useCurrencyStore = create(
  persist(
    (set) => ({
      // State
      currency: 'usd', // 'usd' or 'thb'
      
      // Exchange rate (approximate - will update from API)
      exchangeRate: 35, // 1 USD = 35 THB (approximate)
      
      // Currency symbols
      currencySymbols: {
        usd: '$',
        thb: '฿'
      },
      
      // Actions
      setCurrency: (currency) => set({ currency }),
      
      setExchangeRate: (rate) => set({ exchangeRate: rate }),
      
      // Helper: Get current symbol
      getSymbol: () => {
        const state = useCurrencyStore.getState();
        return state.currencySymbols[state.currency];
      },
      
      // Helper: Format price with currency
      formatPrice: (price) => {
        const state = useCurrencyStore.getState();
        const symbol = state.currencySymbols[state.currency];
        
        if (state.currency === 'thb') {
          return `${symbol}${price.toLocaleString('th-TH', { 
            minimumFractionDigits: 2, 
            maximumFractionDigits: 2 
          })}`;
        }
        
        return `${symbol}${price.toLocaleString('en-US', { 
          minimumFractionDigits: 2, 
          maximumFractionDigits: 2 
        })}`;
      },
      
      // Helper: Convert USD to THB or vice versa
      convertPrice: (price, from, to) => {
        const state = useCurrencyStore.getState();
        if (from === to) return price;
        
        if (from === 'usd' && to === 'thb') {
          return price * state.exchangeRate;
        }
        
        if (from === 'thb' && to === 'usd') {
          return price / state.exchangeRate;
        }
        
        return price;
      }
    }),
    {
      name: 'currency-storage',
      partialize: (state) => ({ 
        currency: state.currency,
        exchangeRate: state.exchangeRate 
      })
    }
  )
);

export default useCurrencyStore;