import { create } from 'zustand';
import { getTopCoins, getAllMarketCoins } from '../services/cryptoService';

const useMarketStore = create((set, get) => ({
  // State
  topCoins: [],
  allCoins: [],
  loading: false,
  error: null,
  lastUpdated: null,

  // Actions
  fetchTopCoins: async (currency = 'usd', limit = 6) => {
    set({ loading: true, error: null });
    try {
      const coins = await getTopCoins(currency, limit);
      set({ 
        topCoins: coins, 
        loading: false,
        lastUpdated: new Date()
      });
    } catch (error) {
      set({ 
        error: error.message || 'Failed to fetch coins', 
        loading: false 
      });
    }
  },

  fetchAllCoins: async (currency = 'usd', page = 1, perPage = 100) => {
    set({ loading: true, error: null });
    try {
      const coins = await getAllMarketCoins(currency, page, perPage);
      set({ 
        allCoins: coins, 
        loading: false,
        lastUpdated: new Date()
      });
    } catch (error) {
      set({ 
        error: error.message || 'Failed to fetch coins', 
        loading: false 
      });
    }
  },

  // Clear error
  clearError: () => set({ error: null }),

  // Refresh data
  refresh: async (currency = 'usd') => {
    const { fetchTopCoins, fetchAllCoins } = get();
    await Promise.all([
      fetchTopCoins(currency),
      fetchAllCoins(currency)
    ]);
  }
}));

export default useMarketStore;