// src/store/portfolioStore.js
import { create } from 'zustand';
import { saveToStorage, loadFromStorage } from '../services/storage';

const STORAGE_KEY = 'crypto-portfolio';

const usePortfolioStore = create((set, get) => ({
  // State
  cash: 10000, // เงินสดเริ่มต้น $10,000
  holdings: {}, // { coinId: { coinSymbol, amount, avgBuyPrice, totalInvested, transactions: [] } }
  transactions: [], // ประวัติทั้งหมด
  initialized: false,

  // Initialize from localStorage
  initialize: () => {
    const saved = loadFromStorage(STORAGE_KEY);
    if (saved) {
      set({
        cash: saved.cash || 10000,
        holdings: saved.holdings || {},
        transactions: saved.transactions || [],
        initialized: true
      });
    } else {
      set({ initialized: true });
    }
  },

  // Save to localStorage
  saveToStorage: () => {
    const { cash, holdings, transactions } = get();
    saveToStorage(STORAGE_KEY, { cash, holdings, transactions });
  },

  // Buy Coin
  buyCoin: (coinId, coinSymbol, coinName, amount, price, note = '') => {
    const { cash, holdings } = get();
    const totalCost = amount * price;

    // Validation: ตรวจสอบเงินสด
    if (totalCost > cash) {
      return {
        success: false,
        message: `Insufficient cash (Need $${totalCost.toFixed(2)}, Have $${cash.toFixed(2)})`
      };
    }

    // สร้าง transaction
    const transaction = {
      id: Date.now(),
      type: 'buy',
      coinId,
      coinSymbol,
      coinName,
      amount,
      price,
      total: totalCost,
      note,
      timestamp: new Date().toISOString()
    };

    // อัพเดท holdings
    const currentHolding = holdings[coinId] || {
      coinSymbol,
      coinName,
      amount: 0,
      totalInvested: 0,
      transactions: []
    };

    const newAmount = currentHolding.amount + amount;
    const newTotalInvested = currentHolding.totalInvested + totalCost;
    const newAvgBuyPrice = newTotalInvested / newAmount;

    const updatedHoldings = {
      ...holdings,
      [coinId]: {
        ...currentHolding,
        amount: newAmount,
        avgBuyPrice: newAvgBuyPrice,
        totalInvested: newTotalInvested,
        transactions: [...currentHolding.transactions, transaction.id]
      }
    };

    // อัพเดท state
    set({
      cash: cash - totalCost,
      holdings: updatedHoldings,
      transactions: [transaction, ...get().transactions]
    });

    get().saveToStorage();

    return {
      success: true,
      message: `Bought ${amount} ${coinSymbol} at $${price.toFixed(2)}`
    };
  },

  // Sell Coin
  sellCoin: (coinId, coinSymbol, amount, price, note = '') => {
    const { cash, holdings } = get();
    const holding = holdings[coinId];

    // Validation: ตรวจสอบว่ามีเหรียญพอขายไหม
    if (!holding || holding.amount < amount) {
      return {
        success: false,
        message: `Insufficient coins (Have ${holding?.amount || 0} ${coinSymbol}, Need ${amount})`
      };
    }

    const totalRevenue = amount * price;

    // คำนวณกำไร/ขาดทุน
    const profitLoss = (price - holding.avgBuyPrice) * amount;

    // สร้าง transaction
    const transaction = {
      id: Date.now(),
      type: 'sell',
      coinId,
      coinSymbol,
      coinName: holding.coinName,
      amount,
      price,
      total: totalRevenue,
      note,
      profitLoss,
      timestamp: new Date().toISOString()
    };

    // อัพเดท holdings
    const newAmount = holding.amount - amount;
    const proportionSold = amount / holding.amount;
    const newTotalInvested = holding.totalInvested * (1 - proportionSold);

    const updatedHoldings = { ...holdings };

    if (newAmount === 0) {
      // ขายหมดแล้ว ลบออกจาก holdings
      delete updatedHoldings[coinId];
    } else {
      updatedHoldings[coinId] = {
        ...holding,
        amount: newAmount,
        totalInvested: newTotalInvested,
        transactions: [...holding.transactions, transaction.id]
      };
    }

    // อัพเดท state
    set({
      cash: cash + totalRevenue,
      holdings: updatedHoldings,
      transactions: [transaction, ...get().transactions]
    });

    get().saveToStorage();

    return {
      success: true,
      message: `Sold ${amount} ${coinSymbol} at $${price.toFixed(2)}`,
      profitLoss
    };
  },

  // คำนวณมูลค่าพอร์ตทั้งหมด (ต้องส่ง current prices เข้ามา)
  getTotalPortfolioValue: (currentPrices) => {
    const { cash, holdings } = get();
    let totalValue = cash;

    Object.entries(holdings).forEach(([coinId, holding]) => {
      const currentPrice = currentPrices[coinId] || 0;
      totalValue += holding.amount * currentPrice;
    });

    return totalValue;
  },

  // คำนวณกำไร/ขาดทุนรวม
  getTotalProfitLoss: (currentPrices) => {
    const { holdings, transactions } = get();
    let totalProfitLoss = 0;

    // กำไร/ขาดทุนจากเหรียญที่ถืออยู่
    Object.entries(holdings).forEach(([coinId, holding]) => {
      const currentPrice = currentPrices[coinId] || 0;
      const currentValue = holding.amount * currentPrice;
      const profitLoss = currentValue - holding.totalInvested;
      totalProfitLoss += profitLoss;
    });

    // เพิ่มกำไร/ขาดทุนจากการขายที่ผ่านมา
    transactions.forEach(tx => {
      if (tx.type === 'sell' && tx.profitLoss) {
        totalProfitLoss += tx.profitLoss;
      }
    });

    return totalProfitLoss;
  },

  // คำนวณ ROI
  getROI: (currentPrices) => {
    const totalValue = get().getTotalPortfolioValue(currentPrices);
    const initialCash = 10000;
    return ((totalValue - initialCash) / initialCash) * 100;
  },

  // ดึงข้อมูลการถือครองเหรียญเฉพาะ
  getHolding: (coinId) => {
    return get().holdings[coinId] || null;
  },

  // Get assets as array (for backward compatibility)
  getAssets: () => {
    const { holdings } = get();
    return Object.entries(holdings).map(([coinId, holding]) => ({
      id: coinId,
      ...holding
    }));
  },

  // Reset portfolio
  resetPortfolio: () => {
    set({
      cash: 10000,
      holdings: {},
      transactions: []
    });
    get().saveToStorage();
  }
}));

export default usePortfolioStore;