import React, { useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import useMarketStore from '../store/marketStore';
import useCurrencyStore from '../store/currencyStore';
import './Dashboard.css';

const Dashboard = () => {
  const { t } = useTranslation();
  const { topCoins, loading, error, fetchTopCoins } = useMarketStore();
  const { currency, formatPrice } = useCurrencyStore();

  useEffect(() => {
    fetchTopCoins(currency, 6);
  }, [currency, fetchTopCoins]);

  // Auto-refresh every 60 seconds
  useEffect(() => {
    const interval = setInterval(() => {
      fetchTopCoins(currency, 6);
    }, 60000);

    return () => clearInterval(interval);
  }, [currency, fetchTopCoins]);

  if (loading && topCoins.length === 0) {
    return (
      <div className="dashboard">
        <div className="loading-container">
          <div className="spinner"></div>
          <p>{t('dashboard.loading')}</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="dashboard">
        <div className="error-container">
          <p className="error-message">{error}</p>
          <button onClick={() => fetchTopCoins(currency, 6)} className="retry-button">
            {t('dashboard.retry')}
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="dashboard">
      {/* Header */}
      <div className="dashboard-header">
        <h1>{t('dashboard.title')}</h1>
        <p className="subtitle">{t('dashboard.subtitle')}</p>
      </div>

      {/* Top Coins Section */}
      <div className="top-coins-section">
        <h2>{t('dashboard.topCoins')}</h2>
        <div className="coins-grid">
          {topCoins.map((coin) => (
            <div key={coin.id} className="coin-card">
              <div className="coin-header">
                <img src={coin.image} alt={coin.name} className="coin-icon" />
                <div className="coin-info">
                  <h3>{coin.name}</h3>
                  <span className="coin-symbol">{coin.symbol.toUpperCase()}</span>
                </div>
              </div>

              <div className="coin-data">
                <div className="data-row">
                  <span className="label">{t('dashboard.price')}:</span>
                  <span className="value price">{formatPrice(coin.current_price)}</span>
                </div>

                <div className="data-row">
                  <span className="label">{t('dashboard.change24h')}:</span>
                  <span className={`value change ${coin.price_change_percentage_24h >= 0 ? 'positive' : 'negative'}`}>
                    {coin.price_change_percentage_24h >= 0 ? '+' : ''}
                    {coin.price_change_percentage_24h?.toFixed(2)}%
                  </span>
                </div>

                <div className="data-row">
                  <span className="label">{t('dashboard.marketCap')}:</span>
                  <span className="value">{formatPrice(coin.market_cap)}</span>
                </div>

                <div className="data-row">
                  <span className="label">{t('dashboard.volume')}:</span>
                  <span className="value">{formatPrice(coin.total_volume)}</span>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Dashboard;