// src/components/portfolio/TransactionHistory.js
import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import usePortfolioStore from '../../store/portfolioStore';
import './TransactionHistory.css';

const TransactionHistory = () => {
  const { t } = useTranslation();
  const { transactions } = usePortfolioStore();
  const [filter, setFilter] = useState('all'); // all, buy, sell

  // Filter transactions
  const filteredTransactions = filter === 'all' 
    ? transactions 
    : transactions.filter(tx => tx.type === filter);

  // Format date
  const formatDate = (timestamp) => {
    const date = new Date(timestamp);
    return date.toLocaleString('en-US', {
      month: 'short',
      day: 'numeric',
      year: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  if (transactions.length === 0) {
    return (
      <div className="transaction-history empty">
        <div className="empty-state">
          <div className="empty-icon">📋</div>
          <h3>No Transactions Yet</h3>
          <p>Your transaction history will appear here</p>
        </div>
      </div>
    );
  }

  return (
    <div className="transaction-history">
      <div className="history-header">
        <h3>{t('transaction.history')}</h3>
        
        {/* Filter Buttons */}
        <div className="filter-buttons">
          <button 
            className={filter === 'all' ? 'active' : ''} 
            onClick={() => setFilter('all')}
          >
            All ({transactions.length})
          </button>
          <button 
            className={filter === 'buy' ? 'active buy' : 'buy'} 
            onClick={() => setFilter('buy')}
          >
            Buy ({transactions.filter(tx => tx.type === 'buy').length})
          </button>
          <button 
            className={filter === 'sell' ? 'active sell' : 'sell'} 
            onClick={() => setFilter('sell')}
          >
            Sell ({transactions.filter(tx => tx.type === 'sell').length})
          </button>
        </div>
      </div>

      <div className="transactions-list">
        {filteredTransactions.map((tx) => (
          <div key={tx.id} className={`transaction-item ${tx.type}`}>
            <div className="transaction-icon">
              {tx.type === 'buy' ? '🛒' : '💸'}
            </div>

            <div className="transaction-details">
              <div className="transaction-main">
                <span className={`transaction-type ${tx.type}`}>
                  {tx.type === 'buy' ? 'BOUGHT' : 'SOLD'}
                </span>
                <span className="transaction-coin">
                  {tx.amount.toFixed(8)} {tx.coinSymbol}
                </span>
              </div>

              <div className="transaction-meta">
                <span className="transaction-date">{formatDate(tx.timestamp)}</span>
                {tx.note && (
                  <span className="transaction-note">📝 {tx.note}</span>
                )}
              </div>
            </div>

            <div className="transaction-values">
              <div className="transaction-price">
                <span className="label">Price:</span>
                <span className="value">${tx.price.toLocaleString(undefined, { 
                  minimumFractionDigits: 2, 
                  maximumFractionDigits: 2 
                })}</span>
              </div>

              <div className="transaction-total">
                <span className="label">Total:</span>
                <span className={`value ${tx.type}`}>
                  {tx.type === 'buy' ? '-' : '+'}${tx.total.toLocaleString(undefined, { 
                    minimumFractionDigits: 2, 
                    maximumFractionDigits: 2 
                  })}
                </span>
              </div>

              {tx.type === 'sell' && tx.profitLoss !== undefined && (
                <div className="transaction-profit">
                  <span className="label">P/L:</span>
                  <span className={`value ${tx.profitLoss >= 0 ? 'positive' : 'negative'}`}>
                    {tx.profitLoss >= 0 ? '+' : ''}${tx.profitLoss.toLocaleString(undefined, { 
                      minimumFractionDigits: 2, 
                      maximumFractionDigits: 2 
                    })}
                  </span>
                </div>
              )}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default TransactionHistory;