import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import useCurrencyStore from '../../store/currencyStore';
import './Navbar.css';

const Navbar = () => {
  const { t, i18n } = useTranslation();
  const location = useLocation();
  const { currency, setCurrency } = useCurrencyStore();

  const toggleLanguage = () => {
    const newLang = i18n.language === 'en' ? 'th' : 'en';
    i18n.changeLanguage(newLang);
  };

  const toggleCurrency = () => {
    const newCurrency = currency === 'usd' ? 'thb' : 'usd';
    setCurrency(newCurrency);
  };

  const isActive = (path) => {
    return location.pathname === path ? 'active' : '';
  };

  return (
    <nav className="navbar">
      <div className="navbar-container">
        {/* Logo */}
        <Link to="/" className="navbar-logo">
          <span className="logo-icon">₿</span>
          <span className="logo-text">Crypto Portfolio</span>
        </Link>

        {/* Navigation Links */}
        <div className="navbar-links">
          <Link to="/" className={`nav-link ${isActive('/')}`}>
            <span className="nav-icon">📊</span>
            <span>{t('nav.dashboard')}</span>
          </Link>
          <Link to="/portfolio" className={`nav-link ${isActive('/portfolio')}`}>
            <span className="nav-icon">💼</span>
            <span>{t('nav.portfolio')}</span>
          </Link>
          <Link to="/markets" className={`nav-link ${isActive('/markets')}`}>
            <span className="nav-icon">🌐</span>
            <span>{t('nav.markets')}</span>
          </Link>
        </div>

        {/* Controls */}
        <div className="navbar-controls">
          {/* Currency Toggle */}
          <button 
            onClick={toggleCurrency} 
            className="control-btn currency-btn"
            title={t('currency.switch')}
          >
            <span className="currency-icon">💱</span>
            <span className="currency-text">{currency.toUpperCase()}</span>
          </button>

          {/* Language Toggle */}
          <button 
            onClick={toggleLanguage} 
            className="control-btn language-btn"
            title="Switch Language"
          >
            <span className="flag-icon">
              {i18n.language === 'en' ? '🇺🇸' : '🇹🇭'}
            </span>
            <span className="language-text">
              {i18n.language === 'en' ? 'EN' : 'TH'}
            </span>
          </button>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;