// src/components/common/SearchableSelect.js
import React, { useState, useRef, useEffect } from 'react';
import './SearchableSelect.css';

const SearchableSelect = ({ options, value, onChange, placeholder }) => {
  const [isOpen, setIsOpen] = useState(false);
  const [searchTerm, setSearchTerm] = useState('');
  const dropdownRef = useRef(null);

  // Close dropdown when clicking outside
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
        setIsOpen(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, []);

  // Filter options based on search
  const filteredOptions = options.filter(option =>
    option.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    option.symbol.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const handleSelect = (option) => {
    onChange(option);
    setIsOpen(false);
    setSearchTerm('');
  };

  return (
    <div className="searchable-select" ref={dropdownRef}>
      <div className="select-input" onClick={() => setIsOpen(!isOpen)}>
        {value ? (
          <div className="selected-option">
            <img src={value.image} alt={value.name} />
            <span>{value.name} ({value.symbol.toUpperCase()})</span>
          </div>
        ) : (
          <span className="placeholder">{placeholder}</span>
        )}
        <span className="arrow">{isOpen ? '▲' : '▼'}</span>
      </div>

      {isOpen && (
        <div className="dropdown">
          <input
            type="text"
            className="search-input"
            placeholder="Search..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            autoFocus
          />
          <div className="options-list">
            {filteredOptions.length > 0 ? (
              filteredOptions.map(option => (
                <div
                  key={option.id}
                  className="option"
                  onClick={() => handleSelect(option)}
                >
                  <img src={option.image} alt={option.name} />
                  <div className="option-info">
                    <span className="option-name">{option.name}</span>
                    <span className="option-symbol">{option.symbol.toUpperCase()}</span>
                  </div>
                  <span className="option-price">${option.current_price.toLocaleString()}</span>
                </div>
              ))
            ) : (
              <div className="no-options">No coins found</div>
            )}
          </div>
        </div>
      )}
    </div>
  );
};

export default SearchableSelect;