// src/pages/Portfolio.js
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import usePortfolioStore from '../store/portfolioStore';
import useMarketStore from '../store/marketStore';
import TransactionForm from '../components/portfolio/TransactionForm';
import HoldingsList from '../components/portfolio/HoldingsList';
import TransactionHistory from '../components/portfolio/TransactionHistory';
import PortfolioSummary from '../components/portfolio/PortfolioSummary';
import './Portfolio.css';

const Portfolio = () => {
  const { t } = useTranslation();
  const { initialize } = usePortfolioStore();
  const { fetchTopCoins, topCoins } = useMarketStore();
  const [activeTab, setActiveTab] = useState('holdings'); // holdings, transactions

  useEffect(() => {
    initialize();
    fetchTopCoins();
  }, []);

  return (
    <div className="portfolio-page">
      <div className="portfolio-header">
        <h1>💼 {t('portfolio.title')}</h1>
      </div>

      {/* Portfolio Summary */}
      <PortfolioSummary />

      <div className="portfolio-content">
        {/* Transaction Form */}
        <div className="transaction-section">
          <TransactionForm coins={topCoins} />
        </div>

        {/* Holdings & History */}
        <div className="portfolio-data">
          {/* Tabs */}
          <div className="tabs">
            <button
              className={activeTab === 'holdings' ? 'active' : ''}
              onClick={() => setActiveTab('holdings')}
            >
              {t('portfolio.holdings')}
            </button>
            <button
              className={activeTab === 'transactions' ? 'active' : ''}
              onClick={() => setActiveTab('transactions')}
            >
              {t('transaction.history')}
            </button>
          </div>

          {/* Tab Content */}
          <div className="tab-content">
            {activeTab === 'holdings' ? (
              <HoldingsList coins={topCoins} />
            ) : (
              <TransactionHistory />
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Portfolio;