// src/components/portfolio/HoldingsList.js
import React from 'react';
import { useTranslation } from 'react-i18next';
import usePortfolioStore from '../../store/portfolioStore';
import './HoldingsList.css';

const HoldingsList = ({ coins }) => {
  const { t } = useTranslation();
  const { holdings } = usePortfolioStore();

  // สร้าง price map จาก coins
  const priceMap = {};
  coins.forEach(coin => {
    priceMap[coin.id] = coin.current_price;
  });

  // แปลง holdings เป็น array และเพิ่มข้อมูลราคาปัจจุบัน
  const holdingsArray = Object.entries(holdings).map(([coinId, holding]) => {
    const currentPrice = priceMap[coinId] || 0;
    const currentValue = holding.amount * currentPrice;
    const profitLoss = currentValue - holding.totalInvested;
    const profitLossPercent = holding.totalInvested > 0 
      ? (profitLoss / holding.totalInvested) * 100 
      : 0;

    // หา coin image
    const coinData = coins.find(c => c.id === coinId);

    return {
      coinId,
      ...holding,
      currentPrice,
      currentValue,
      profitLoss,
      profitLossPercent,
      image: coinData?.image || ''
    };
  });

  // เรียงตามมูลค่ามากไปน้อย
  holdingsArray.sort((a, b) => b.currentValue - a.currentValue);

  if (holdingsArray.length === 0) {
    return (
      <div className="holdings-list empty">
        <div className="empty-state">
          <div className="empty-icon">📊</div>
          <h3>No Holdings Yet</h3>
          <p>Start by buying your first cryptocurrency!</p>
        </div>
      </div>
    );
  }

  return (
    <div className="holdings-list">
      <div className="holdings-header">
        <h3>{t('portfolio.holdings')}</h3>
        <p className="holdings-count">{holdingsArray.length} {t('portfolio.coins')}</p>
      </div>

      <div className="holdings-table">
        <div className="table-header">
          <div className="col-coin">{t('portfolio.coin')}</div>
          <div className="col-amount">{t('portfolio.amount')}</div>
          <div className="col-price">{t('portfolio.avgBuyPrice')}</div>
          <div className="col-price">{t('portfolio.currentPrice')}</div>
          <div className="col-value">{t('portfolio.total')}</div>
          <div className="col-profit">{t('portfolio.profitLoss')}</div>
        </div>

        <div className="table-body">
          {holdingsArray.map((holding) => (
            <div key={holding.coinId} className="table-row">
              <div className="col-coin">
                <img src={holding.image} alt={holding.coinName} />
                <div className="coin-details">
                  <span className="coin-name">{holding.coinName}</span>
                  <span className="coin-symbol">{holding.coinSymbol}</span>
                </div>
              </div>

              <div className="col-amount">
                <span className="amount-value">{holding.amount.toFixed(8)}</span>
              </div>

              <div className="col-price">
                <span>${holding.avgBuyPrice.toLocaleString(undefined, { 
                  minimumFractionDigits: 2, 
                  maximumFractionDigits: 2 
                })}</span>
              </div>

              <div className="col-price">
                <span>${holding.currentPrice.toLocaleString(undefined, { 
                  minimumFractionDigits: 2, 
                  maximumFractionDigits: 2 
                })}</span>
              </div>

              <div className="col-value">
                <span className="value-primary">
                  ${holding.currentValue.toLocaleString(undefined, { 
                    minimumFractionDigits: 2, 
                    maximumFractionDigits: 2 
                  })}
                </span>
                <span className="value-invested">
                  Invested: ${holding.totalInvested.toLocaleString(undefined, { 
                    minimumFractionDigits: 2, 
                    maximumFractionDigits: 2 
                  })}
                </span>
              </div>

              <div className="col-profit">
                <span className={`profit-value ${holding.profitLoss >= 0 ? 'positive' : 'negative'}`}>
                  {holding.profitLoss >= 0 ? '+' : ''}${holding.profitLoss.toLocaleString(undefined, { 
                    minimumFractionDigits: 2, 
                    maximumFractionDigits: 2 
                  })}
                </span>
                <span className={`profit-percent ${holding.profitLossPercent >= 0 ? 'positive' : 'negative'}`}>
                  {holding.profitLossPercent >= 0 ? '+' : ''}{holding.profitLossPercent.toFixed(2)}%
                </span>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default HoldingsList;