import React, { useState, useEffect, useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { getAllMarketCoins } from '../services/cryptoService';
import useCurrencyStore from '../store/currencyStore';
import './Markets.css';

const Markets = () => {
  const { t } = useTranslation();
  const { currency, formatPrice } = useCurrencyStore();
  
  // State
  const [coins, setCoins] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [searchQuery, setSearchQuery] = useState('');
  const [filterType, setFilterType] = useState('all'); // all, gainers, losers
  const [sortConfig, setSortConfig] = useState({ key: 'market_cap_rank', direction: 'asc' });
  const [currentPage, setCurrentPage] = useState(1);
  const [refreshing, setRefreshing] = useState(false);
  
  const coinsPerPage = 20;

  // Fetch coins
  const fetchCoins = async (showRefreshIndicator = false) => {
    try {
      if (showRefreshIndicator) {
        setRefreshing(true);
      } else {
        setLoading(true);
      }
      setError(null);
      
      const data = await getAllMarketCoins(currency, 1, 100);
      setCoins(data);
    } catch (err) {
      setError(t('markets.error') || 'Failed to load market data');
      console.error('Error fetching coins:', err);
    } finally {
      setLoading(false);
      setRefreshing(false);
    }
  };

  // Initial fetch
  useEffect(() => {
    fetchCoins();
  }, [currency]);

  // Auto-refresh every 60 seconds
  useEffect(() => {
    const interval = setInterval(() => {
      fetchCoins(true);
    }, 60000);

    return () => clearInterval(interval);
  }, [currency]);

  // Filter coins
  const filteredCoins = useMemo(() => {
    let filtered = [...coins];

    // Search filter
    if (searchQuery) {
      filtered = filtered.filter(coin =>
        coin.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
        coin.symbol.toLowerCase().includes(searchQuery.toLowerCase())
      );
    }

    // Type filter
    if (filterType === 'gainers') {
      filtered = filtered.filter(coin => coin.price_change_percentage_24h > 0);
    } else if (filterType === 'losers') {
      filtered = filtered.filter(coin => coin.price_change_percentage_24h < 0);
    }

    return filtered;
  }, [coins, searchQuery, filterType]);

  // Sort coins
  const sortedCoins = useMemo(() => {
    let sorted = [...filteredCoins];

    if (sortConfig.key) {
      sorted.sort((a, b) => {
        let aValue = a[sortConfig.key];
        let bValue = b[sortConfig.key];

        // Handle null values
        if (aValue === null) return 1;
        if (bValue === null) return -1;

        if (aValue < bValue) {
          return sortConfig.direction === 'asc' ? -1 : 1;
        }
        if (aValue > bValue) {
          return sortConfig.direction === 'asc' ? 1 : -1;
        }
        return 0;
      });
    }

    return sorted;
  }, [filteredCoins, sortConfig]);

  // Pagination
  const paginatedCoins = useMemo(() => {
    const startIndex = (currentPage - 1) * coinsPerPage;
    const endIndex = startIndex + coinsPerPage;
    return sortedCoins.slice(startIndex, endIndex);
  }, [sortedCoins, currentPage]);

  const totalPages = Math.ceil(sortedCoins.length / coinsPerPage);

  // Handle sort
  const handleSort = (key) => {
    setSortConfig(prev => ({
      key,
      direction: prev.key === key && prev.direction === 'asc' ? 'desc' : 'asc'
    }));
  };

  // Handle page change
  const handlePageChange = (page) => {
    setCurrentPage(page);
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  // Format large numbers
  const formatNumber = (num) => {
    if (num >= 1e12) return `${(num / 1e12).toFixed(2)}T`;
    if (num >= 1e9) return `${(num / 1e9).toFixed(2)}B`;
    if (num >= 1e6) return `${(num / 1e6).toFixed(2)}M`;
    if (num >= 1e3) return `${(num / 1e3).toFixed(2)}K`;
    return num.toFixed(2);
  };

  // Render sort icon
  const renderSortIcon = (key) => {
    if (sortConfig.key !== key) {
      return <span className="sort-icon">↕</span>;
    }
    return sortConfig.direction === 'asc' ? 
      <span className="sort-icon active">↑</span> : 
      <span className="sort-icon active">↓</span>;
  };

  if (loading) {
    return (
      <div className="markets-page">
        <div className="loading-container">
          <div className="spinner"></div>
          <p>{t('markets.loading') || 'Loading market data...'}</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="markets-page">
        <div className="error-container">
          <p className="error-message">{error}</p>
          <button onClick={() => fetchCoins()} className="retry-button">
            {t('markets.retry') || 'Retry'}
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="markets-page">
      {/* Header */}
      <div className="markets-header">
        <div className="header-content">
          <h1>{t('markets.title') || 'Cryptocurrency Markets'}</h1>
          <p className="subtitle">
            {t('markets.subtitle') || 'Track prices and market data for top cryptocurrencies'}
          </p>
        </div>
        {refreshing && (
          <div className="refresh-indicator">
            <div className="spinner-small"></div>
            <span>{t('markets.refreshing') || 'Updating...'}</span>
          </div>
        )}
      </div>

      {/* Controls */}
      <div className="markets-controls">
        {/* Search */}
        <div className="search-container">
          <input
            type="text"
            placeholder={t('markets.search') || 'Search coins...'}
            value={searchQuery}
            onChange={(e) => {
              setSearchQuery(e.target.value);
              setCurrentPage(1);
            }}
            className="search-input"
          />
          <span className="search-icon">🔍</span>
        </div>

        {/* Filters */}
        <div className="filter-buttons">
          <button
            className={`filter-btn ${filterType === 'all' ? 'active' : ''}`}
            onClick={() => {
              setFilterType('all');
              setCurrentPage(1);
            }}
          >
            {t('markets.all') || 'All'}
          </button>
          <button
            className={`filter-btn ${filterType === 'gainers' ? 'active' : ''}`}
            onClick={() => {
              setFilterType('gainers');
              setCurrentPage(1);
            }}
          >
            {t('markets.gainers') || 'Gainers'} 📈
          </button>
          <button
            className={`filter-btn ${filterType === 'losers' ? 'active' : ''}`}
            onClick={() => {
              setFilterType('losers');
              setCurrentPage(1);
            }}
          >
            {t('markets.losers') || 'Losers'} 📉
          </button>
        </div>

        {/* Results count */}
        <div className="results-count">
          {sortedCoins.length} {t('markets.coins') || 'coins'}
        </div>
      </div>

      {/* Table - Desktop */}
      <div className="markets-table-container desktop-only">
        <table className="markets-table">
          <thead>
            <tr>
              <th onClick={() => handleSort('market_cap_rank')}>
                # {renderSortIcon('market_cap_rank')}
              </th>
              <th>{t('markets.coin') || 'Coin'}</th>
              <th onClick={() => handleSort('current_price')} className="text-right">
                {t('markets.price') || 'Price'} {renderSortIcon('current_price')}
              </th>
              <th onClick={() => handleSort('price_change_percentage_24h')} className="text-right">
                24h % {renderSortIcon('price_change_percentage_24h')}
              </th>
              <th onClick={() => handleSort('market_cap')} className="text-right">
                {t('markets.marketCap') || 'Market Cap'} {renderSortIcon('market_cap')}
              </th>
              <th onClick={() => handleSort('total_volume')} className="text-right">
                {t('markets.volume') || 'Volume (24h)'} {renderSortIcon('total_volume')}
              </th>
            </tr>
          </thead>
          <tbody>
            {paginatedCoins.map((coin) => (
              <tr key={coin.id} className="coin-row">
                <td className="rank">{coin.market_cap_rank}</td>
                <td className="coin-info">
                  <img src={coin.image} alt={coin.name} className="coin-image" />
                  <div className="coin-names">
                    <span className="coin-name">{coin.name}</span>
                    <span className="coin-symbol">{coin.symbol.toUpperCase()}</span>
                  </div>
                </td>
                <td className="text-right price">
                  {formatPrice(coin.current_price)}
                </td>
                <td className="text-right">
                  <span className={`change ${coin.price_change_percentage_24h >= 0 ? 'positive' : 'negative'}`}>
                    {coin.price_change_percentage_24h >= 0 ? '+' : ''}
                    {coin.price_change_percentage_24h?.toFixed(2)}%
                  </span>
                </td>
                <td className="text-right market-cap">
                  {formatPrice(coin.market_cap)}
                </td>
                <td className="text-right volume">
                  {formatPrice(coin.total_volume)}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* Cards - Mobile */}
      <div className="markets-cards mobile-only">
        {paginatedCoins.map((coin) => (
          <div key={coin.id} className="coin-card">
            <div className="card-header">
              <div className="coin-info">
                <img src={coin.image} alt={coin.name} className="coin-image" />
                <div className="coin-names">
                  <span className="coin-name">{coin.name}</span>
                  <span className="coin-symbol">{coin.symbol.toUpperCase()}</span>
                </div>
              </div>
              <span className="rank">#{coin.market_cap_rank}</span>
            </div>
            
            <div className="card-body">
              <div className="card-row">
                <span className="label">{t('markets.price') || 'Price'}:</span>
                <span className="value price">{formatPrice(coin.current_price)}</span>
              </div>
              
              <div className="card-row">
                <span className="label">24h:</span>
                <span className={`value change ${coin.price_change_percentage_24h >= 0 ? 'positive' : 'negative'}`}>
                  {coin.price_change_percentage_24h >= 0 ? '+' : ''}
                  {coin.price_change_percentage_24h?.toFixed(2)}%
                </span>
              </div>
              
              <div className="card-row">
                <span className="label">{t('markets.marketCap') || 'Market Cap'}:</span>
                <span className="value">{formatPrice(coin.market_cap)}</span>
              </div>
              
              <div className="card-row">
                <span className="label">{t('markets.volume') || 'Volume'}:</span>
                <span className="value">{formatPrice(coin.total_volume)}</span>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Pagination */}
      {totalPages > 1 && (
        <div className="pagination">
          <button
            onClick={() => handlePageChange(currentPage - 1)}
            disabled={currentPage === 1}
            className="page-btn"
          >
            ← {t('markets.previous') || 'Previous'}
          </button>
          
          <div className="page-numbers">
            {[...Array(totalPages)].map((_, index) => {
              const page = index + 1;
              // Show first, last, current, and adjacent pages
              if (
                page === 1 ||
                page === totalPages ||
                (page >= currentPage - 1 && page <= currentPage + 1)
              ) {
                return (
                  <button
                    key={page}
                    onClick={() => handlePageChange(page)}
                    className={`page-number ${currentPage === page ? 'active' : ''}`}
                  >
                    {page}
                  </button>
                );
              } else if (page === currentPage - 2 || page === currentPage + 2) {
                return <span key={page} className="ellipsis">...</span>;
              }
              return null;
            })}
          </div>
          
          <button
            onClick={() => handlePageChange(currentPage + 1)}
            disabled={currentPage === totalPages}
            className="page-btn"
          >
            {t('markets.next') || 'Next'} →
          </button>
        </div>
      )}
    </div>
  );
};

export default Markets;