// src/components/portfolio/PortfolioSummary.js
import React from 'react';
import { useTranslation } from 'react-i18next';
import usePortfolioStore from '../../store/portfolioStore';
import useMarketStore from '../../store/marketStore';
import './PortfolioSummary.css';

const PortfolioSummary = () => {
  const { t } = useTranslation();
  const { cash, holdings, getTotalPortfolioValue, getTotalProfitLoss, getROI } = usePortfolioStore();
  const { topCoins } = useMarketStore();

  // สร้าง price map
  const currentPrices = {};
  topCoins.forEach(coin => {
    currentPrices[coin.id] = coin.current_price;
  });

  const totalValue = getTotalPortfolioValue(currentPrices);
  const totalProfitLoss = getTotalProfitLoss(currentPrices);
  const roi = getROI(currentPrices);
  const holdingsCount = Object.keys(holdings).length;
  const investedAmount = totalValue - cash - totalProfitLoss;

  return (
    <div className="portfolio-summary">
      <div className="summary-card primary">
        <div className="summary-icon">💰</div>
        <div className="summary-content">
          <h3>{t('portfolio.totalValue')}</h3>
          <p className="value-large">${totalValue.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</p>
        </div>
      </div>

      <div className="summary-card">
        <div className="summary-icon">💵</div>
        <div className="summary-content">
          <h3>{t('portfolio.cash')}</h3>
          <p className="value">${cash.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</p>
        </div>
      </div>

      <div className="summary-card">
        <div className="summary-icon">📊</div>
        <div className="summary-content">
          <h3>{t('portfolio.holdings')}</h3>
          <p className="value">{holdingsCount} {t('portfolio.coins')}</p>
          <p className="sub-value">
            ${investedAmount.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })} invested
          </p>
        </div>
      </div>

      <div className="summary-card">
        <div className="summary-icon">{totalProfitLoss >= 0 ? '📈' : '📉'}</div>
        <div className="summary-content">
          <h3>{t('portfolio.profitLoss')}</h3>
          <p className={`value ${totalProfitLoss >= 0 ? 'positive' : 'negative'}`}>
            {totalProfitLoss >= 0 ? '+' : ''}${totalProfitLoss.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}
          </p>
          <p className={`sub-value ${roi >= 0 ? 'positive' : 'negative'}`}>
            ROI: {roi >= 0 ? '+' : ''}{roi.toFixed(2)}%
          </p>
        </div>
      </div>
    </div>
  );
};

export default PortfolioSummary;