import api from './api';

// Get top cryptocurrencies
export const getTopCoins = async (currency = 'usd', limit = 6) => {
  try {
    const response = await api.get('/coins/markets', {
      params: {
        vs_currency: currency,
        order: 'market_cap_desc',
        per_page: limit,
        page: 1,
        sparkline: false,
        price_change_percentage: '24h'
      }
    });
    return response.data;
  } catch (error) {
    console.error('Error fetching top coins:', error);
    throw error;
  }
};

// Get all market coins (for Markets page)
export const getAllMarketCoins = async (currency = 'usd', page = 1, perPage = 100) => {
  try {
    const response = await api.get('/coins/markets', {
      params: {
        vs_currency: currency,
        order: 'market_cap_desc',
        per_page: perPage,
        page: page,
        sparkline: false,
        price_change_percentage: '24h,7d'
      }
    });
    return response.data;
  } catch (error) {
    console.error('Error fetching market coins:', error);
    throw error;
  }
};

// Get coin details
export const getCoinDetails = async (coinId) => {
  try {
    const response = await api.get(`/coins/${coinId}`, {
      params: {
        localization: false,
        tickers: false,
        community_data: false,
        developer_data: false
      }
    });
    return response.data;
  } catch (error) {
    console.error('Error fetching coin details:', error);
    throw error;
  }
};

// Search coins
export const searchCoins = async (query) => {
  try {
    const response = await api.get('/search', {
      params: { query }
    });
    return response.data.coins;
  } catch (error) {
    console.error('Error searching coins:', error);
    throw error;
  }
};

// Get coin price history (for charts)
export const getCoinHistory = async (coinId, currency = 'usd', days = 7) => {
  try {
    const response = await api.get(`/coins/${coinId}/market_chart`, {
      params: {
        vs_currency: currency,
        days: days,
        interval: days === 1 ? 'hourly' : 'daily'
      }
    });
    return response.data;
  } catch (error) {
    console.error('Error fetching coin history:', error);
    throw error;
  }
};

// Get current exchange rate (USD to THB)
export const getExchangeRate = async () => {
  try {
    const response = await api.get('/simple/price', {
      params: {
        ids: 'tether',
        vs_currencies: 'thb'
      }
    });
    return response.data.tether.thb;
  } catch (error) {
    console.error('Error fetching exchange rate:', error);
    return 35; // Fallback rate
  }
};